.profile__info-subtitle {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media screen and (max-width: 920px){
  .profile__info-subtitle{
    font-size: 14px;
    line-height: 16.94px;
    text-align: center;
  }
}
